import React, {ChangeEvent} from 'react';
import {colorService, IColorModel} from "./colors.service";

export class ColorsTable extends React.Component {
    state = {
        searchColor: ''
    };

    handleChange = (e: ChangeEvent<HTMLInputElement>): void => {
        const hexColor = e.target.value;
        const searchColor = hexColor.length === 6 ? `#${hexColor}` : '';
        this.setState({ searchColor });

        colorService.setSearchColor(hexColor.length === 6 ? `#${hexColor}` : '');
    };

    render() {
        return (
            <div style={{ display: 'flex', justifyContent: 'space-between'}}>
                <div style={{width: '40%'}}>
                    <table>
                        <thead>
                        <tr>
                            <td>DMC</td>
                            <td>Gamma</td>
                            <td>Anchor</td>
                            <td>Madeira</td>
                            <td>Цвет</td>
                        </tr>
                        </thead>
                        <tbody>
                        {colorService.filteredColors.map(((row: IColorModel, index: number) => {
                            return (
                                <tr key={index}>
                                    <td>{row.DMC}</td>
                                    <td>{row.Gamma}</td>
                                    <td>{row.Anchor}</td>
                                    <td>{row.Madeira}</td>
                                    <td style={{backgroundColor: row.color}}/>
                                </tr>
                            )
                        }))}
                        </tbody>
                    </table>
                </div>
                <div style={{width: '60%'}}>
                    <input type="text" onChange={this.handleChange}/>
                </div>
            </div>

        );
    }
}

